function FormField({ type, id, fname, onChange, caption, value, error, ...attr}){
    return (
      <section >
      <label className="my-4 text-center">{caption}</label>
      <input
          className="mt-1 block px-10 border-none bg-gray-100 h-11 rounded-xl shadow-lg hover:bg-pink-100 focus:bg-pink-100 focus:ring-0"
          type={type}
          id={id}
          name={fname}
          onChange={onChange}
          value={value}
          {...attr}
      />
        {(error && true) ? (<section>{error}</section>):null }
      </section>
    )
  }
  

export default FormField;