import React, { useState } from "react";
import Form from "../Forms/Form";
import FormAction from "../Forms/FormAction";
import FormField from "../Forms/FormField";


function Login(){
    const [formData, setFormData] = useState({
      username : "",
      password : "",
      usernameError : "",
      passwordError: ""
    });
  
    //const _self = this;
    const myOnChange = function(e){
      const { name, value } = e.currentTarget;
      //validaciones
      let error = null;
      if (name ==="password"){
        error = (/^(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[^\w\d\s:])([^\s]){8,32}$/).test(value) ? null :"Contraseña Inválida";
      }
      //
      setFormData({ ...formData, [name]: value , [name + "Error"]: error});
    }
    return (
      <section className=" bg-pink-200 flex border justify-center items-center rounded-lg  w-2/4 h-96 md:h-96 md:w-1/3 sm:w-full shadow-lg">
        <section className="flex flex-col justify-center sm:m-11 md:m-16 items-center justify-items-center">
          <div className="mb-10 flex justify-center items-center justify-items-center">
            <h2 className="text-center text-lg">INICIO DE SESIÓN</h2>
          </div>

      <Form>
        <FormField
          type="text"
          id="username"
          fname="username"
          onChange={myOnChange}
          caption="Correo Electrónico"
          value={formData.username}
          error={formData.usernameError}
          placeholder="example15@mcsf.com"
        ></FormField>
        <FormField
          type="password"
          id="password"
          fname="password"
          onChange={myOnChange}
          caption="Contraseña"
          value={formData.password}
          error={formData.passwordError}
          placeholder="Pretty_Fish1234"
        ></FormField>
        <FormAction
          caption="Enviar"
          onClick={
            (e)=>{
              e.preventDefault();
              e.stopPropagation();
              const { username, password } = formData;
              alert(`Usuario:${username} Psswd:${password}`);
            }
          }
        >
        </FormAction>
      </Form>

      <section class="flex mt-2 items-center text-center">
        
        <a href="#" class="block font-medium 	text-decoration: underline font-serif text-sm text-black w-full">¿Ya tienes una cuenta?</a>

      </section>

     </section>
     </section>

    );
  }
  
export default Login;