function FormAction({caption,onClick}){
    let styles = [
        "w-full", 
        "flex",
        "items-center",
        "justify-center",
        "px-8",
        "py-1",
        "my-6",
        "border",
        "border-transparent",
        "text-base",
        "font-medium",
        "rounded-lg",
        "bg-pink-700",
        "text-white",
        "hover:bg-pink-100",
        "hover:text-black",
        "md:py-4",
        "md:text-lg",
        "md:px-10",
    ];
    return (
        <button
      onClick={onClick}
      className={styles.join(" ")}
    >
      {caption}
    </button>
        );
}

export default FormAction;
